var _this;
import wInput from '@/components/watch-login/watch-input.vue' //input
import wButton from '@/components/watch-login/watch-button.vue' //button
import userService from '@/service/UserService.js'

export default {
	data() {
		return {
			mail: '',
			password: '',
			code: '',
			disable: true,
			isRotate: false, //是否加载旋转
			canSendMail: false
		}
	},
	components:{
		wInput,
		wButton,
	},
	mounted() {
		_this= this;
	},
	watch: {
		mail(val) {
			this.canSendMailCode();
			this.checkParam();
		},
		password(val){
			this.checkParam();
		},
		code(val) {
			this.checkParam();
		}
	},
	methods: {
		
		getVerCode(){
			//获取验证码
			if (!this.canSendMail) {
				uni.showToast({
				    icon: 'none',
					position: 'bottom',
				    title: '请正确填写邮箱地址'
				});
				return;
			}
			userService.sendCode(this.mail, 3);
			this.$refs.runCode.$emit('runCode'); //触发倒计时（一般用于请求成功验证码后调用）
		},
	    resetPwd() {
			//注册
			if(this.isRotate){
				//判断是否加载中，避免重复点击请求
				return false;
			}
			
			_this.isRotate = true
			userService.resetPwd(this.mail, this.password, this.code)
			
			setTimeout(function(){
				_this.isRotate = false
			}, 4000)
			
	    },
		
		checkMail() {
			let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			return reg.test(this.mail)
		},
		
		checkParam() {
			let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			if (this.checkMail() && this.password != null && this.password.length >= 5 && this.code != null && this.code.length == 6)
				this.disable = false;
			else
				this.disable = true;
		},
		
		canSendMailCode(){
			if (this.checkMail())
				this.canSendMail = true
			else
				this.canSendMail = false
		}
	}
}
